import { Grid } from '@material-ui/core';
import { FabelioRootTextField } from '../Shared/textfields';
import FurnitureStyleSelection from './FurnitureStyle';
import DeliveryTimeSelection from './DeliveryTime';
import './index.css';

const Navbar = (props) => {
  const { 
    furniture_styles,
    onFilterKeyChanges, 
    onStylesChanges, 
    onDeliveryTimeChanges } = props;

  return (
    <Grid 
      container 
      direction="row" 
      justify="center" 
      className="navbar-root"
    >
      <Grid 
        item
        container 
        xs={12}
        spacing={3}
        direction="row" 
        justify="space-between"
      >
        <Grid 
          item 
          xs={6}
        >
          <FabelioRootTextField 
            id="filled-search" 
            className="fieldbox" 
            label="Search Furniture" 
            type="search" 
            fullWidth
            onChange={(event) => onFilterKeyChanges(event.target.value)}
          />
        </Grid>

        <Grid 
          item 
          container 
          spacing={3}
        >
          <Grid 
            item 
            xs={6}
          >
             <FurnitureStyleSelection 
              furniture_styles={furniture_styles} 
              onStylesChanges={onStylesChanges}
              
            />
          </Grid>

          <Grid 
            item 
            xs={6}
          >
             <DeliveryTimeSelection
              onDeliveryTimeChanges={onDeliveryTimeChanges}
            />
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  )
};

export default Navbar;