/**
 * Convert Number to String Currency in Rupiah (Rp)
 * @returns {string}
 */
export function formatNumberToRupiahCurrency(num) {
  var p = num.toFixed(2).split(".");
  return "Rp " + p[0].split("").reverse().reduce(function (acc, num, i) {
    return num === "-" ? acc : num + (i && !(i % 3) ? "." : "") + acc;
  }, "");
}


/**
 * Filter Products by Given Keywords
 * @returns {array}
 */
export const searchProductByKey = (searchFilterKey = '', products) => {
  return products.filter((product) => product.name.toLowerCase().includes(searchFilterKey.toLowerCase()));
};

/**
 * Filter Products by Given Sets of Furniture Styles
 * @returns {array}
 */
export const filterProductStyle = (filterStyles, products) => {
  let filterProduct = [];

  products.map((product) => {
    const { name, furniture_style } = product;

    furniture_style.map((style) => {
      if (filterStyles.includes(style) && !filterProduct.find((filterPrd) => filterPrd.name === name )) {
        filterProduct.push(product);
      }
    });
  });

  return filterProduct;
}

/**
 * Filter Products by Given Sets of Delivery Time Range
 * @returns {array}
 */
export const filterProductDeliveryTime = (filterDeliveryTimes, products) => {
  let highestDays = Math.max.apply(Math, filterDeliveryTimes.map(({ value }) => parseInt(value)));
  if (highestDays > 30) {
    return products.filter(({ delivery_time }) => delivery_time >= highestDays);
  } else if (highestDays <= 30) {
    return products.filter(({ delivery_time }) => delivery_time <= highestDays);
  } else {
    return [];
  }
}