import { Grid } from '@material-ui/core'
import Product from './Product';
import './index.css';

const Contents = ({ products }) => {
  return (
    <Grid 
      container 
      spacing={3}
      direction="row" 
      justify="flex-start" 
      className="content-root" 
    >
      {
        (products.length) ? (
          products.map((product, index) => <Product key={index} product={product}/>)
        ) : (<></>)
      }
    </Grid>
  )
};

export default Contents;