import React, { useEffect } from 'react';
import { Grid, Select, MenuItem } from '@material-ui/core'
import { FabelioSelectField, GreenCheckbox } from '../../Shared/textfields';
import './index.css';

const deliveryTimes = [
  { name: "1 Week", value: 7 },
  { name: "2 Weeks", value: 14 },
  { name: "3 Weeks", value: 21 },
  { name: "1 Month", value: 30 },
  { name: "More than 1 Month", value: 31 }
]

const DeliveryTimeSelection = ({ onDeliveryTimeChanges }) => {
  const [filterDeliveryTimes, setFilterDeliveryTimes] = React.useState([]);
  
  useEffect(() => {
    onDeliveryTimeChanges(filterDeliveryTimes);
  }, [filterDeliveryTimes])

  const handleChange = (event) => {
    setFilterDeliveryTimes(event.target.value);
  };

  return (
    <Select
      fullWidth
      multiple
      input={<FabelioSelectField />}
      value={filterDeliveryTimes}
      onChange={handleChange}
      renderValue={(selected) => selected.map((time) => time.name).join(", ")}
      className="fieldbox"
    >
      {
        deliveryTimes.map((timePreference) => {
          const { name } = timePreference;
          return (
            <MenuItem key={name} value={timePreference}>
              <Grid 
                container
                direction="row"
                justify="space-between"
                className="select-item"
              >
                <p>{name}</p>
                <GreenCheckbox checked={filterDeliveryTimes.indexOf(timePreference) > -1 } />
                {/* <GreenCheckbox checked={filterDeliveryTimes.indexOf(value) > -1 } /> */}
                {/* myArray.map(function(e) { return e.hello; }).indexOf('stevie'); */}
              </Grid>
            </MenuItem>
          )
        })
      }
    </Select>
  )

};

export default DeliveryTimeSelection;