import React, { useState, useEffect } from 'react';
import Navbar from './Components/Navbar';
import Contents from './Components/Contents';
import { searchProductByKey, filterProductStyle, filterProductDeliveryTime } from './Lib';
import './App.css';

const fetchProductUrl = 'https://www.mocky.io/v2/5c9105cb330000112b649af8';

const App = () => {
  const [products, setProducts] = useState([]);
  const [furniture_styles, setFurnitureStyles] = useState([]);
  const [searchFilterKey, setSearchFilterKey] = useState('');
  const [filterStyles, setFilterStyles] = useState([])
  const [filterDeliveryTimes, setFilterDeliveryTimes] = useState([]);
  const [productsFiltered, setProductFilter] = useState([]);

  useEffect(() => {
    fetchProducts();
  }, []);

  useEffect(() => {
    setProductFilter(searchProductByKey(searchFilterKey, products));
  }, [searchFilterKey]);
  
  useEffect(() => {
    const searchedProducts = searchProductByKey(searchFilterKey, products);
    
    if (!filterStyles.length) {
      setProductFilter(searchedProducts);
    } else {
      setProductFilter(filterProductStyle(filterStyles, searchedProducts));
    }

  }, [filterStyles]);

  useEffect(() => {
    if (productsFiltered.length) {
      setProductFilter(filterProductDeliveryTime(filterDeliveryTimes, productsFiltered));
    } else if (!filterDeliveryTimes.length) {
      setProductFilter([]);
    } else {
      setProductFilter(filterProductDeliveryTime(filterDeliveryTimes, products));
    }
  }, [filterDeliveryTimes]);

  const fetchProducts = async () => {
    const fetchingResponse = await fetch(fetchProductUrl);
    const response = await fetchingResponse.json();
    const { products, furniture_styles } = response;

    if (furniture_styles.length) {
      setFurnitureStyles(furniture_styles);
    }

    if (products.length) {
      setProducts(products);
    }
  };

  return (
    <div className="root">
      <Navbar 
        furniture_styles={furniture_styles}
        onFilterKeyChanges={value => setSearchFilterKey(value)}
        onStylesChanges={value => setFilterStyles(value)}
        onDeliveryTimeChanges={value => setFilterDeliveryTimes(value)}
      />
      <Contents 
        products={(!productsFiltered.length) ? (searchFilterKey.length || filterStyles.length || filterDeliveryTimes.length) ? (productsFiltered) : (products) : (productsFiltered)}
      />
    </div>
  );
};

export default App;
